@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'IBM Plex Mono', monospace;
}


.App {
  background-color: #212121;
  color: #ececec;
  font-size: 2rem;
  height: 100vh;
  padding: 1.8rem;
  overflow-y: scroll;
}
.CommandsHistory{
    list-style: none;
    padding: 0;
    margin: 0;
}

.responseLine{
    margin-bottom: 2rem;
    white-space: pre-wrap;
}
.commandLine{
    display: flex;
    margin-bottom: 1rem;
}

.userInput{
    margin-left: 2rem;
    flex-grow: 1;
    background-color: inherit;
    color: inherit;
    font-size: inherit;
    border: none;
    font-family: inherit;
}

.userInput:focus{
    outline: none;
}
